import React, { useState, useEffect } from "react";
import people from "./data";
import { FaChevronLeft, FaChevronRight, FaQuoteRight } from "react-icons/fa";

const Review = () => {
  const [person, setPerson] = useState({});

  const getElement = (id) => {
    const newPerson = people.filter((per) => per.id === id);
    return newPerson[0];
  };

  useEffect(() => {
    const firstElement = people[0];
    setPerson(() => getElement(firstElement.id));
  }, []);

  const getPrePerson = (id) => {
    if (id === people[0].id) {
      setPerson(() => getElement(people.length));
    } else {
      setPerson(() => getElement(id - 1));
    }
  };

  const getNextPerson = (id) => {
    if (id === people.length) {
      setPerson(() => getElement(people[0].id));
    } else {
      setPerson(() => getElement(id + 1));
    }
  };

  const getRandomPerson = (id) => {
    let randomNumber = Math.floor(
      Math.random() * (people.length - people[0].id + 1) + people[0].id
    );
    if (randomNumber === id) {
      randomNumber += 1;
    }
    if (randomNumber === people.length + 1) randomNumber = people[0].id;
    setPerson(() => getElement(randomNumber));
  };

  return (
    <article className="review">
      <div className="img-container">
        <img src={person.image} alt={person.name} className="person-img" />
        <span className="quote-icon">
          <FaQuoteRight />
        </span>
      </div>
      <h4 className="author">{person.name}</h4>
      <p className="job">{person.job}</p>
      <p className="info">{person.text}</p>
      <div className="button-container">
        <button className="prev-btn" onClick={() => getPrePerson(person.id)}>
          <FaChevronLeft />
        </button>
        <button className="next-btn" onClick={() => getNextPerson(person.id)}>
          <FaChevronRight />
        </button>
      </div>
      <button className="random-btn" onClick={() => getRandomPerson(person.id)}>
        surprise me
      </button>
    </article>
  );
};

export default Review;
